import Header from "../components/Header";
import Footer from "../components/Footer";
import Appointment from "../components/Appointment";
// import Carrousel from "../components/Carrousel";
import Testimonials from "../components/Testimonials";
import About from "../components/About";
import img2 from '../assets/images/dentistry2.png';
import MapContainer from "../components/SimpleMap";


export default function Home() {


    return (
        <>
            <Header />
            <div className="text-white items-center absolute md:bottom-52 bottom-72 left-10 font-['Quicksand']"  >
                <h1 className=" md:text-9xl text-3xl text-left">Better Dentistry</h1>
                <p className="md:text-3xl text-xl">
                Join us to a fun and friendly dental environment. Our professionals are working so hard to see smile on your face that you deserve! We are dedicated about our duties.
                </p>
            </div>
            <img
                className="object-fill w-full max-h-[650px]"
                src={img2}
                alt="image slide 2"
            />
            {/* <Carrousel/> */}
            <About />
            <Testimonials />
            <Appointment />
            <MapContainer/>
            <Footer />
        </>
    )
}
